/* Main.Js */

import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import timestamp from 'unix-timestamp';

import { sendDataToAPI, getDataFromAPI } from './assets/js/ApiRequest';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/custom.css';
import './assets/css/navigation.css';

import Sidebar from './components/sc-sidebar-alt';
import Topbar from './components/sc-topbar-alt';
import Footer from './components/sc-footer';

import Dashboard from './views/dashboard';
import Error from './views/error';
 
class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            toggle: true,
            response: {sensor: null,
                performance: null,
            }
        };
        
        this.toggle = this.toggle.bind(this);
        timestamp.round = true;
    };

    componentDidMount() {
        let [sendPeriode, getPeriode] = [30, 10];
        let [Tsend, Tget] = [
            timestamp.now(),
            timestamp.now()
        ];

        this.setState(
            {response: getDataFromAPI()}
        );

        setInterval(
            () => {
                let Tnow = timestamp.now();
                if (Tnow > Tsend) {
                    sendDataToAPI(); Tsend += sendPeriode;
                } else if (Tnow > Tget) {
                    Tget += getPeriode;
                    this.setState(
                        {response: getDataFromAPI()}
                    );
                }
            }, 1000
        );
    };

    toggle(event) {
        event.preventDefault();
        this.setState(
            {toggle: !this.state.toggle}
        );
    };

    render() {
        return (
            <div id="content-wrapper" className="d-flex flex-column">
                <div id="content" className={`sb-nav-fixed ${this.state.toggle ? null : "sb-sidenav-toggled"}`}>
                    <Topbar func={this.toggle} {...this.props}/>
                    
                    <div id="layoutSidenav">
                        <Sidebar/>
                        <div id="layoutSidenav_content">
                            <Switch>
                                <Route exact path="/" component={Dashboard} />
                                <Route exact path="/help" component={Error} />
                            </Switch>
                            <Footer />
                        </div>
                    </div>
                    
                </div> 
            </div>
        );
    };
};

export default Main;
