import React from 'react';

function Card ({header, value=0, badge=true}) {
    return (
        <div className="card shadow border-left-asphalt border-0 rounded-0 py-2">
            <div className="card-body pt-2 pb-0">
                <div className="d-block px-2">

                    <div className="d-flex justify-content-between mx-auto mb-2">
                        <small className="d-inline font-weight-bold text-midnight">{header}</small>
                        {badge && <span className="badge badge-success px-4">
                            Optimal</span>}
                    </div>
 
                    <div className="d-flex justify-content-end align-text-top mx-auto">
                        <h1 className="d-inline display-4 mb-1">{value}</h1>
                        <h1 className="d-inline ml-1">°C</h1>
                    </div>

                </div>
            </div>
        </div>
    );
};

export function CardBody ({header, value=0, badge=true}) {
    return (
        <div className="d-block px-2">
            <div className="d-flex justify-content-between mx-auto mb-2">
                <small className="d-inline font-weight-bold text-midnight">{header}</small>
                {badge && <span className="badge badge-success px-4">
                    Optimal</span>}
            </div>

            <div className="d-flex justify-content-end align-text-top mx-auto mb-2">
                <h1 className="d-inline display-3 mb-1">{value}</h1>
                <h1 className="d-inline ml-1">°C</h1>
            </div>
        </div>
    );
};

export default Card;