import React from 'react';
import Nav from '../components/sc-nav';
import SensorData from '../assets/js/ApiRequest';

import Thermal from './thermal';
import {error} from './error';

function Dashboard() {
    return (
        <div className="container-fluid mt-2 pt-2">
            <div className="d-flex align-items-center justify-content-between mb-3">
                <div className="d-none d-sm-inline h3 text-dark my-auto">Dashboard</div>
                <div className="d-none d-sm-inline">
                    <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button" onClick={SensorData}>
                        <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                        <span className="text px-4">Generate Report</span>
                    </button>
                </div>
            </div>

            <div className="d-none d-sm-block">
                <nav className="nav nav-tabs mb-2" role="tablist">
                    <a className="nav-item nav-link text-midnight font-weight-bold small active" data-toggle="tab" href="#thermal" role="tab"
                        aria-controls="thermal" aria-selected="true">Thermal Management</a>
                    <a className="nav-item nav-link text-midnight font-weight-bold small" data-toggle="tab" href="#energy" role="tab"
                        aria-controls="energy" aria-selected="false">Energy Management</a>

                    <small className="nav-item nav-link disabled text-dark ml-auto">
                        <strong className="text-midnight">Last Updated:</strong> 00:00:00 AM
                    </small>
                </nav>

                <div className="tab-content">
                    <div className="tab-pane fade show active" id="thermal" role="tabpanel" aria-labelledby="v-pills-thermal-tab">
                        <Thermal /></div>
                    <div className="tab-pane fade" id="energy" role="tabpanel" aria-labelledby="v-pills-energy-tab">
                        {error ('Under Development')}
                    </div>
                </div>
            </div>

            <div className="d-none d-md-block mb-3"><Nav /></div>
            
            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">
                {error ('Mobile view is not supported yet :(')}
            </div>
        </div>
    );
};

export default Dashboard;