import React from 'react';

function Progress ({header, value=0}) {
    const style = {
        width: `${value}%`
    };

    return (
        <div className="d-block px-2 mb-3">
            <div className="d-flex justify-content-between mx-auto mb-2">
                <small className="font-weight-bold text-midnight">{header}</small>
                <small className="font-weight-bold">{value}</small>
            </div>

            <div className="progress">
                <div className="progress-bar progress-bar-striped bg-info" role="progressbar" aria-valuenow={value} aria-valuemin="0" aria-valuemax="100"
                    style={style}></div>
            </div>
        </div>
    );
};

export default Progress;