import React, { Component } from 'react';
import Chart from 'react-apexcharts';

const font = {
    name: {size: '12px'}, value: {size: '32px'},
    fontFamily: 'Nunito, Arial, sans-serif'
};

const gauge = {
    startAngle: -135,
    endAngle: 135
};

class ApexChartGauge extends Component {
    constructor({
        bound={g:[1,75], y:[75,90], r:[90,100]},
        ...props
    }) {
        
        super(props);
        this.state = {
            series: [0],
            options: {
                series: [0],
                labels: [this.props.chartLabel],
                chart: {
                    height: this.props.chartHeight,
                    type: 'radialBar',
                    toolbar: {
                        show: false
                    }
                },
                plotOptions: {
                    radialBar: {
                        startAngle: gauge.startAngle,
                        endAngle: gauge.endAngle,
                        hollow: {
                            size: '67',
                            margin: 0,
                            position: 'front',
                            background: '#FFFFFF',
                            dropShadow: {
                                enabled: true,
                                opacity: 0.2,
                                top: 3,
                                left: 0,
                                blur: 4
                            }
                        },
                        track: {
                            margin: 0,
                            dropShadow: {
                                enabled: true,
                                opacity: 0.2,
                                top: -3,
                                left: 0,
                                blur: 4
                            }
                        },
                        dataLabels: {
                            showOn: 'always',
                            name: {
                                offsetY: -10,
                                fontFamily: font.fontFamily,
                                fontSize: font.name.size,
                                color: '#858796',
                                show: true
                            },
                            value: {
                                formatter: (value) => parseFloat (value),
                                fontFamily: font.fontFamily,
                                fontSize: font.value.size,
                                color: '#858796',
                                show: true
                            }
                        }
                    }
                },
                fill: {
                    colors: [
                        function ({value}) {
                            if (value>=bound.g[0] && value<=bound.g[1]) {
                                return '#2ECC71';
                            } else if (value>=bound.y[0] && value<=bound.y[1]) {
                                return '#F39C12';
                            } else if (value>=bound.r[0] && value<=bound.r[1]) {
                                return '#E74C3C';
                            }
                            /* Value out of defined bound. */
                            else return '#40739E';
                        }
                    ],
                    opacity: (1),
                    type: 'gradient',
                    gradient: {
                        shade: 'dark',
                        type: 'vertical',
                        gradientToColors: ['#40739E'],
                        inverseColors: (false),
                        stops: [0, 100]
                    }
                },
                stroke: {
                    lineCap: 'round'
                },
                grid: {
                    padding: {
                        bottom: 16
                    }
                },
            }
        };
    };

    render() {
        const series = [this.props.series];
        const chartHeight = this.props.chartHeight;

        return (
            <div className="d-flex align-items-top justify-content-center">
                <Chart options={this.state.options} series={series} type="radialBar"
                    height={chartHeight}/>
            </div>
        );
    };
};

export default ApexChartGauge;