import React from 'react';
import LocalStorageService from '../assets/js/utils/LocalStorageService';

function Topbar({func, ...props}) {
    function logout() {LocalStorageService.clear();
        props.history.push('/auth');
    };

    return (
        <div>
            {/* <!-- Topbar --> */}
            <nav className="sb-topnav navbar navbar-expand navbar-dark bg-asphalt shadow">
                <div className="navbar-brand">TKL Smart Cabinet</div>
                <button className="btn btn-link btn-sm" id="sidebarToggle" onClick={func}>
                    <i className="fas fa-bars"></i>
                </button>

                {/* <!-- Navbar Search--> */}
                <form className="navbar-search d-none d-md-block form-inline">
                    <div className="input-group mx-3">
                        <input className="form-control form-control-sm" type="text" aria-label="Search" aria-describedby="basic-addon"
                            style={{width: '240px'}} />
                        <div className="input-group-append">
                            <button className="btn btn-warning btn-sm" type="button">
                                <i className="fas fa-search fa-sm"></i>
                            </button>
                        </div>
                    </div>
                </form>

                {/* <!-- Navbar--> */}
                <ul className="navbar-nav d-none d-sm-block ml-auto">
                    <li className="nav-item dropdown d-flex justify-content-center align-items-center">
                        
                        <div className="nav-link" id="userDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"> 
                            <div className="d-inline h2 text-white mr-3">
                                <i className="fas fa-user"></i>
                            </div>

                            <div className="nav-item d-inline-block">
                                <span className="d-block text-white text-xs">Welcome Back!</span>
                                <a className="d-block dropdown-toggle text-white font-weight-bold small" href="#">
                                    {LocalStorageService.getUser()}
                                    &nbsp;
                                </a>
                            </div>
                        </div>

                        {/* <!-- Dropdown - User Information --> */}
                        <div className="dropdown dropdown-menu dropdown-menu-right shadow animated--grow-in" aria-labelledby="userDropdown">
                            <div className="dropdown-header text-midnight">User Options</div>

                            <div className="dropdown-item navbar-text py-1 mb-1">
                                <h5 className="d-block text-center text-dark mb-1">{LocalStorageService.getUser()}</h5>
                                <span className="d-block badge badge-success px-4">General Administrator</span>
                            </div>
                            
                            <div className="dropdown-divider"></div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-user mr-2 text-midnight"></i>
                                Profile</div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-cogs mr-2 text-midnight"></i>
                                Account Settings</div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-list mr-2 text-midnight"></i>
                                Activity Log</div>
                                
                            <div className="dropdown-divider"></div>
                            <div className="dropdown-item" data-toggle="modal" data-target="#logoutModal">
                                <i className="fas fa-fw fa-sign-out-alt mr-2 text-midnight"></i>Logout
                            </div>
                        </div>
                    </li>
                </ul>
            </nav>

            {/* <!-- Logout Modal--> */}
            <div className="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header modal-title font-weight-bold" id="modalLabel">End Session?</div>
                        <div className="modal-body">Select the <strong>Logout</strong> button below to end your session.</div>
                        <div className="modal-footer">
                            <button className="btn btn-sm btn-secondary font-weight-bold px-3" type="button" data-dismiss="modal">Cancel</button>
                            <button className="btn btn-sm btn-primary font-weight-bold px-3" type="button" data-dismiss="modal" onClick={logout}>
                                Logout</button>
                        </div>
                    </div>
                </div>
            </div>

            {/* <!-- End of Topbar --> */}
        </div>
    );
};

export default Topbar;