import React from 'react';
import Main from './Main';
import Auth from './Auth';

import { Switch } from 'react-router-dom';
import { Public, Private } from './assets/js/utils/AccessRoute';
import ApiProvider from './assets/js/contexts/ApiContext';

function App() {
    return (
        <div>
            <ApiProvider>
                <Switch>
                    <Public exact path="/auth" component={Auth} />
                    <Private exact path="/" component={Main} />
                </Switch>
            </ApiProvider>
      </div>
    );
};

export default App;
