import axios from 'axios';
import LocalStorageService from './utils/LocalStorageService';
import timestamp from 'unix-timestamp';

timestamp.round = true;
const requestContext = {
    sensor: [
        't_coil',
        't_leaving_coil',
        't_leaving_blower',
        't_supply',
        't_return',
        'r_humidity',
    ],
    performance: [
        'rci_Lo',
        'rci_Hi',
        'pue'
    ]
};

export function sendDataToAPI() {
    Object.keys (requestContext).forEach (
        (key) => {
            const payload = {'unix': timestamp.now()};
            requestContext[key].forEach (f => payload[f] = Math.round (
                Math.random() * 1e+04) / 100
            );

            axios
                .post (`http://sancaliar82.pythonanywhere.com/api/${key}`, payload)
                .catch (error => console.log(error))
        }
    );
};

export function getDataFromAPI() {
    const json = {};
    
    Object.keys (requestContext).forEach (
        (key) => {
            axios
                .get (`http://sancaliar82.pythonanywhere.com/api/${key}`)
                .then (response => {
                    const stringJson = JSON.stringify(response.data[0]);
                    LocalStorageService.setItem(
                        key,
                        stringJson
                    );

                    json[key] = response.data[0]
                })
        }
    );
    return json;
};

const SensorData = () => {
    axios
        .get ('http://sancaliar82.pythonanywhere.com/api/sensor')
        .then (response => console.log (response.data[0]))
};

export default SensorData;