/* *Local Storage Service */
import CryptoJS from 'crypto-js';
const scrKey = 'Oc4539yCAo9B1mM-75N6yRM2quXZsLQf6kgV0PSuwKo';

const usrKey = {
    refreshToken: CryptoJS.SHA256('refreshToken').toString(
        CryptoJS.enc.Base64),
    accessToken: CryptoJS.SHA256('accessToken').toString(
        CryptoJS.enc.Base64),
    user: CryptoJS.SHA256('user').toString(
        CryptoJS.enc.Base64)
};

const LocalStorageService = (function() {
    return ({
        setRefreshToken: refreshToken => localStorage.setItem(
            usrKey.refreshToken, 
            AES.encrypt(refreshToken)
        ),
        setAccessToken: accessToken => localStorage.setItem(
            usrKey.accessToken,
            AES.encrypt(accessToken)
        ),
        setUser: user => localStorage.setItem(
            usrKey.user,
            AES.encrypt(user)
        ),

        setItem: (key, item) => {
            const object = (typeof item === 'object' && item !== null ?
                JSON.stringify(item) : item);

            const encryptedObj = AES.encrypt(object);
            localStorage.setItem(
                key,
                encryptedObj
            );
        },
        getItem: (key) => {
            try {
                const decryptedJson = JSON.parse(
                    getDecrypted(key)
                );
                return (decryptedJson!==null ? decryptedJson
                    : false);
            } catch(e) {
                return getDecrypted(key);
            }
        },
        
        clear:() => localStorage.clear(),  
        isAuthenticated:() => {
            return getDecrypted(usrKey.accessToken)!==null;
        },
        getAccessToken:() => {
            return getDecrypted(usrKey.accessToken);
        },
        getRefreshToken:() => {
            return getDecrypted(usrKey.refreshToken);
        },
        getUser:() => {
            return getDecrypted(usrKey.user);
        }
    });
})();

const AES = (function() {
    return ({
        encrypt: object => {
            return CryptoJS.AES.encrypt(object, scrKey).toString();
        },
        decrypt_utf8: object => {
            return CryptoJS.AES.decrypt(object, scrKey).toString(
                CryptoJS.enc.Utf8);
        }
    });
})();

function getDecrypted(key) {
    return (
        localStorage.getItem(key)!=null ? AES.decrypt_utf8(
            localStorage.getItem(key)) : null
    );
};

export default LocalStorageService;